import React from "react";
import { Card, CardBody, Media, Row, Col, Button } from "reactstrap";
import axiosConfig from "../../../../axiosConfig";
import { history } from "../../../../history";
import { Route } from "react-router-dom";
class ViewVendor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
    };
  }
  componentDidMount() {
    // console.log(this.props.match.params);
    let { id } = this.props.match.params;
    console.log("vendor id ", id);
    axiosConfig
      .get(`/viewone_vendor/${id}`)
      .then(response => {
        console.log(response.data.data);
        //console.log(response.data.data.shop_name);
        this.setState({ data: response.data.data });
      })
      .catch(error => {
        console.log(error.response.data);
      });
  }
  render() {
    return (
      <React.Fragment>
        <Row>
          <Col sm="12">
            <Card>
              <Row className="m-2">
                <Col>
                  <h1 col-sm-6 className="float-left">
                    All ViewVendor Details
                  </h1>
                </Col>
                <Col>
                  <Route
                    render={({ history }) => (
                      <Button
                        className=" btn btn-danger float-right"
                        onClick={() =>
                          history.push("/app/freshlist/vendor/vendorList")
                        }
                      >
                        Back
                      </Button>
                    )}
                  />
                </Col>
              </Row>
              <CardBody>
                <Row className="mx-0" col="12">
                  <Col className="pl-0" sm="12" lg="6">
                    <Media className="d-sm-flex d-block">
                      <Media body>
                        <Row className="ml-4">
                          <Col sm="9" md="12" lg="12">
                            <div className="users-page-view-table">
                              <div className="d-flex user-info">
                                <div className="user-info-title font-weight-bold">
                                  Shop Name
                                </div>
                                <div className="text-truncate">
                                  <span>{this.state.data.shop_name}</span>
                                </div>
                              </div>
                              <div className="d-flex user-info">
                                <div className="user-info-title font-weight-bold">
                                  Email
                                </div>
                                <div className="text-truncate">
                                  <span>{this.state.data.email}</span>
                                </div>
                              </div>
                              <div className="d-flex user-info">
                                <div className="user-info-title font-weight-bold">
                                  Phone
                                </div>
                                <div className="text-truncate">
                                  <span>{this.state.data.phone}</span>
                                </div>
                              </div>
                              <div className="users-page-view-table">
                                <div className="d-flex user-info">
                                  <div className="user-info-title font-weight-bold">
                                    Status
                                  </div>
                                  <div>{this.state.data.status}</div>
                                </div>
                              </div>
                            </div>
                          </Col>
                        </Row>
                      </Media>
                    </Media>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}
export default ViewVendor;
